<template>
    <el-select 
        v-model="publisherType" 
        placeholder="请选择" 
        @change='togglePublisherType' 
        clearable
        class='itemInput'>
        <el-option
        v-for="item in publisherTypeList"
        :key="item.code"
        :label="item.name"
        :value="item.code">
        </el-option>
    </el-select>
</template>

<script>
export default {
    data() {
        return {
            publisherType: ''
        }
    },
    props: ['publisherTypeList','type'],
    methods: {
        togglePublisherType () {
            this.$emit('togglePublisherType',this.publisherType,this.type);
        }
    }
}
</script>

