// 推荐

import { JSON_FETCH } from '@/axios/index';

// 推荐内容列表
const recommend_cotnent_list_api = `/recommend/contentList`;

// 添加内容推荐
const recommend_content_add_api = `/recommend/contentAdd`;

// 删除内容推荐
const recommend_content_delete_api = `/recommend/contentDelete`;

// 推荐出版社列表
const recommend_publisher_list_api = `/recommend/publisherList`;

// 添加出版社推荐
const recommend_publisher_add_api = `/recommend/publisherAdd`;

// 删除出版社推荐
const recommend_publisher_delete_api = `/recommend/publisherDelete`;

/**
 * 添加内容推荐
 * @param { Object } params
 * @param { String } params.contentId 推荐id
 */
export function get_recommend_content_add_api(params) {
    return JSON_FETCH({
        method: 'post',
        url: recommend_content_add_api,
        data: params
    })
}

/**
 * 删除内容推荐
 * @param { Object } params
 * @param { String } params.id 管理员id
 */
export function get_recommend_content_delete_api(params) {
    return JSON_FETCH({
        method: 'post',
        url: recommend_content_delete_api,
        data: params
    })
}

/**
 * 推荐内容列表
 * @param { Object } params
 * @param { String } params.pageNo  当前页
 * @param { String } params.pageSize 当前数量
 */
export function get_recommend_cotnent_list_api(params) {
    return JSON_FETCH({
        method: 'post',
        url: recommend_cotnent_list_api,
        data: params
    })
}


/**
 * 推荐出版社列表
 * @param { Object } params
 * @param { String } params.pageNo  当前页
 * @param { String } params.pageSize 当前数量
 */
export function get_recommend_publisher_list_api(params) {
    return JSON_FETCH({
        method: 'post',
        url: recommend_publisher_list_api,
        data: params
    })
}

/**
 * 添加推荐出版社
 * @param { Object } params
 * @param { String } params.id 管理员id
 */
export function get_recommend_publisher_add_api(params) {
    return JSON_FETCH({
        method: 'post',
        url: recommend_publisher_add_api,
        data: params
    })
}
/**
 * 删除出版社推荐
 * @param { Object } params
 * @param { String } params.contentId 推荐id
 */
export function get_recommend_publisher_delete_api(params) {
    return JSON_FETCH({
        method: 'post',
        url: recommend_publisher_delete_api,
        data: params
    })
}