<template>
    <div>
        <div class="baseTop">
            <div>
                <slelect-common 
                    :publisherTypeList='publisherTypeList'
                    @togglePublisherType='togglePublisherType'>
                </slelect-common>
                <el-input
                    class="serchInput itemInput"
                    placeholder="请输入内容"
                    prefix-icon="el-icon-search"
                    v-model="postData.keyword"
                    @keyup.enter.native="search"
                    clearable>
                </el-input>
                <el-select 
                    v-model="postData.hasRecommend" 
                    clearable 
                    placeholder="请选择" 
                    class="itemInput" 
                    @change="isRecommend"
                >
                    <el-option label="已推荐" value="1"></el-option>
                    <el-option label="未推荐" value="0"></el-option>
                </el-select>
            </div>
            <el-button type="primary" @click="add">添加</el-button>
        </div>
        <!-- 列表数据 -->
        <el-table
            border
            :data="publisherList"
            style="width: 100%">
            <el-table-column
                prop="name"
                label="出版社名称">
            </el-table-column>
            <el-table-column
                prop="piperType"
                width="180"
                label="出版社类型">
            </el-table-column>
            <el-table-column
                prop="ptemail"
                label="秘邮地址"
                width="180">
            </el-table-column>
            <el-table-column 
                            label="操作"
                            width="350"
                            align='center'>
                <template slot-scope="scope">
                    <el-button
                    size="mini"
                    @click="checkContent(scope.row)">查看内容</el-button>
                    <el-button
                    size="mini"
                    @click="check(scope.row)">查看订阅</el-button>
                    <el-button
                    size="mini"
                    :type="scope.row.hasRecommend == 1 ? 'warning' :'primary'"
                    @click="recommendItem(scope.row,scope.$index)">{{scope.row.hasRecommend == 1 ? '取消推荐' : '推荐' }}</el-button>
                    <el-button
                    size="mini"
                    type="danger"
                    @click="handleDelete(scope.row)">删除</el-button>
                </template>
            </el-table-column>
        </el-table>
        <!-- 删除弹框 -->
        <el-dialog
            title="删除"
            :visible.sync="isShowDeleteDialog"
            width="30%"
            :before-close="cancel">
            <span v-if="itemData">是否确认删除<span class="tips">{{itemData.name}}</span>出版社</span>
            <span slot="footer" class="dialog-footer">
                <el-button @click="cancel">取 消</el-button>
                <el-button type="danger" @click="deleteItem">删 除</el-button>
            </span>
        </el-dialog>
        <!-- 添加弹框 -->
        <el-dialog
            title='添加'
            :visible.sync="isShowAddDialog"
            width="50%"
            :before-close="cancel">
            <div class="addItem">
                <span>出版社类型:</span>
                <slelect-common 
                    :publisherTypeList='publisherTypeList'
                    @togglePublisherType='togglePublisherType'
                    :type='true'>
                </slelect-common>
            </div>
            <div class="addItem">
                <span>出版社名称：</span>
                <el-input
                    class="itemInput"
                    placeholder="请输入出版社名称"
                    v-model="publisherName"
                    clearable>
                </el-input>
            </div>
            <div class="addItem">
                <span>出版社邮箱：</span>
                <el-input
                    class="itemInput"
                    placeholder="请输入出版社邮箱"
                    v-model="publisherMail"
                    clearable>
                </el-input>
            </div>
            <div class="addItem">
                <span>创建人邮箱：</span>
                <el-input
                    class="itemInput"
                    placeholder="请输入创建人邮箱"
                    v-model="publisherUserId"
                    clearable>
                </el-input>
            </div>
            <span slot="footer" class="dialog-footer">
                <el-button @click="cancel">取 消</el-button>
                <el-button type="primary" @click="addPublisher">添 加</el-button>
            </span>
        </el-dialog>
        <!-- 分页 -->
        <page :total='total' :pageNo='postData.pageNo' @togglePages='togglePages'></page>
    </div>
</template>

<script>
import { 
    get_publisher_add_api, 
    get_publisher_delete_api, 
    get_publisher_list_api, 
    get_publisher_type_api 
} from '@/api/publisher';
import { get_recommend_publisher_add_api, get_recommend_publisher_delete_api } from '@/api/recommend';
import page from '@/components/page';
import slelectCommon from '@/components/publisher/slelectCommon';
export default {
    data() {
        return {
            publisherUserId: "", // 创建人邮箱
            publisherName: "", // 出版社名称
            publisherMail: "", // 出版社邮箱 
            itemData: null, // 当前数据
            isShowAddDialog: false, // 添加弹框
            isShowDeleteDialog: false, // 删除弹框
            postData: {
                keyword: "",
                pageNo: 1,
                pageSize: 10,
                piperType: 0,
                hasRecommend: ''
            }, // 传递参数
            total: '', // 总条数
            publisherType: '',
            publisherTypeList : [], // 出版社类型列表
            publisherList: [], //出版社列表数据
        }
    },
    methods: {
        isRecommend() {
            this.postData.pageNo = 1;
            this.getList(this.postData);
        },
        // 获取数据
        getList (postData) {
            get_publisher_list_api(postData).then(res => {
                this.publisherList = res.list;
                this.total = res.total;
            })
        },
        // 分页
       togglePages (page) {
           this.postData.pageNo = page;
           this.getList(this.postData);
       },
        // 搜索
        search () {
            this.postData.pageNo = 1;
            this.getList(this.postData);
        },
        // 取消
        cancel() {
            this.isShowAddDialog = false;
            this.isShowDeleteDialog = false;
        },
       // 添加出版社
        add () {
            this.publisherName = '';
            this.publisherMail = '';
            this.postData.piperType = 0;
            this.publisherUserId = '';
            this.isShowAddDialog = true;
        },
        // 删除出版社
       handleDelete (row) {
           this.itemData = row;
           this.isShowDeleteDialog = true;
       },
        // 确认删除当前条
        deleteItem() {
            let obj = {
                publisherId: this.itemData.publisherId,
                // userId: this.postData.userId
            }
            get_publisher_delete_api(obj).then(() => {
                this.formatData('删除成功','success');
                this.getList(this.postData);
            })
        },
        // 确认添加出版社
        addPublisher () {
            if ( this.postData.piperType == 0) {
                this.$message({
                    message: '请选择出版社类型',
                    type: 'warning'
                });
                return ;
            } 
            if ( this.publisherName == '' ) {
                this.$message({
                    message: '请输入出版社名称',
                    type: 'warning'
                });
                return ;
            }
            if (this.publisherMail == '') {
                this.$message({
                    message: '请输入出版社邮箱',
                    type: 'warning'
                });
                return ;
            }
            if (this.publisherUserId == '') {
                this.$message({
                    message: '请输入创建人邮箱',
                    type: 'warning'
                });
                return ;
            }
            let obj = {
                piperType: this.postData.piperType,
                publishMail: this.publisherMail,
                publishName: this.publisherName,
                userId: this.publisherUserId
            }
            get_publisher_add_api(obj).then(() => {
                this.formatData('添加出版社成功','success');
            }).catch(() => {
                this.formatData('添加出版社成功','success');
            })
        },
        // 查看订阅
        check (row) {
            this.$router.push({
                path: `/subcription/${row.publisherId}`
            })
        },
        // 查看内容
        checkContent(row) {
            this.$router.push({
                path: `/content/${row.publisherId}`
            })
        },
        // 切换出版社类型
       togglePublisherType (publisherType, type) {
           this.postData.piperType = publisherType;
           this.postData.pageNo = 1;
           if ( !type ) {
               this.getList(this.postData);
           }
       },
        // 推荐
       recommendItem(row,index) {
            // 推荐   
           if ( row.hasRecommend == 0 ) {
               get_recommend_publisher_add_api({publisherId:row.publisherId}).then(() => {
                   this.publisherList[index].hasRecommend = 1;
               })
            // 取消推荐
           } else {
                get_recommend_publisher_delete_api({reid:row.publisherId}).then(() => {
                   this.publisherList[index].hasRecommend = 0;
               })
           }
       },
        // 数据格式化
        formatData (msg,type) {
            this.publisherName = '';
            this.publisherMail = '';
            this.postData.piperType = 0;
            this.publisherUserId = '';
            this.$message({
                message: msg,
                type: type
            });
            this.isShowDeleteDialog = false;
            this.isShowAddDialog = false;
            this.postData.pageNo = 1;
        }
    },
    // 获取列表数据
    created () {
        this.getList(this.postData);
        get_publisher_type_api().then(res => {
            this.publisherTypeList = res;
        })
    },
    components: {
        page,
        slelectCommon
    }
}
</script>

<style scoped lang='scss'>
.addItem{
    margin-bottom: 20px;
    display: flex;
    align-items: center;
    span{
        display: block;
        width:150px;
    }
}
</style>