// 出版社

import { JSON_FETCH } from '@/axios/index';

// 添加出版社
const publisher_add_api = `/publisher/add`;

// 删除出版社
const publisher_delete_api = `/publisher/delete`;

// 出版社列表
const publisher_list_api = `/publisher/list`;

// 出版社类型
const publisher_type_api='/publisher/getPiperType'

/**
 * 添加出版社
 * @param { Object } params
 * @param { String } params.publishName 出版社名字
 * @param { String } params.publishMail 出版社邮箱
 * @param { String } params.piperType 出版社类型
 * @param { String } params.userId 出版社id
 */
export function get_publisher_add_api(params) {
    return JSON_FETCH({
        method: 'post',
        url: publisher_add_api,
        data: params
    })
}

/**
 * 删除出版社
 * @param { Object } params
 * @param { String } params.publisherUserId 管理员id
 * @param { String } params.userId 出版社id
 */
export function get_publisher_delete_api(params) {
    return JSON_FETCH({
        method: 'post',
        url: publisher_delete_api,
        data: params
    })
}

/**
 * 出版社列表
 * @param { Object } params
 * @param { String } params.keyword 关键字
 * @param { String } params.pageNo  当前页
 * @param { String } params.pageSize 当前数量
 * @param { String } params.piperType 出版社类型
 * @param { String } params.userId 用户id
 */
export function get_publisher_list_api(params) {
    return JSON_FETCH({
        method: 'post',
        url: publisher_list_api,
        data: params
    })
}

/**
 * 出版社类型
 */
export function get_publisher_type_api() {
    return JSON_FETCH({
        method: 'post',
        url: publisher_type_api
    })
}